nav {
    background: $blue-light2;
    position: relative;
    .owl-item {
        height: 135px; }
    .header-titular {
        position: absolute;
        top: -1rem;
        width: 100%; }
    .nav-categories {
        padding: 0rem 0;
        a {
            text-decoration: none;
            display: block;
            height: 100%;
            padding: 1.5rem 0 0rem;
            transition: .5s ease background;
            &:hover,
            &.active {
                background: $green-light; } }
        p {
            color: $black;
            font-size: 8px;
            line-height: 1.25em;
            text-align: center;
            text-transform: uppercase;
            margin: 0; }
