.separate2 {
    width: 100%;
    height: 1.5rem; }
.separate3 {
    width: 100%;
    height: 2rem; }
.img-card {
    width: auto !important; }
.bgazul {
    background: $blue !important; }
.melon {
    background: $melon !important; }
.celeste {
    background: $blue-light2 !important; }
.owl-days {
    .owl-stage {
        .owl-item {
            padding-bottom: 2rem; } } }
.bannerMed,
.bannerFot {
    position: relative;
    top: 0rem;
    margin-top: 1rem;
    margin-bottom: 3rem;
    @media screen and (min-width: 992px) {
        top: -5rem;
        margin-top: 0rem; }

    a {
        display: block; }
    img {
        width: 100%;
        object-fit: cover; } }
.bannerFot {
    top: 0;
    margin-bottom: 3rem;
    margin-top: .5rem; }
.separate {
    width: 100%;
    height: .5rem; }
.wrapper-blue {
    background: $blue;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 12rem; }
.box-days,
.box-days-desktop {
    position: relative;
    z-index: 200;
    display: none;
    @media screen and (min-width: 992px) {
        display: block; }
    &.posTop {
        top: -10rem; }
    &.posTop2 {
        top: -8rem; }
    &.posTop3 {
        margin-top: 3rem; }
    &.posTop4 {
        margin-top: 2rem; } }
.box-days-desktop {
    display: block;
    @media screen and (min-width: 992px) {
        display: none; } }
.bggray {
    background: $gray; }
.bggreen-light {
    background: $green-light; }
.flotante {
    position: absolute; }
// Cartas
.card {
    // margin-right: .5rem
    padding: 3.5rem 0rem 0rem;
    width: 100%;
    position: relative;
    @media screen and ( min-width: 992px ) {
        width: 31%; }
    // overflow: hidden
    &.p1 {
        padding-top: 1rem;
        padding-right: 1rem; }
    &:last-child {
        margin-right: 0; }
    .sombra {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: -2.5rem;
        @media screen and (min-width: 1200px) {
            bottom: -2.75rem; }
        opacity: .4;
        img {
            width: 250px;
            @media screen and (min-width: 1200px) {
                width: auto; } } }
    .card-body {
        margin: auto;
        text-align: center;
        &.white,
        &.green {
            text-align: left;
            h5 {
                @extend %archer-semiBold;
                color: white;
                text-align: left;
                &.small {
                    font-size: 25px; }
                span {
                    @extend %akrobat;
                    font-size: 110px;
                    line-height: 5.5rem; } } }
        &.green {
            h5 {
                color: $green-light;
                span {
                    color: $green-light; } } }

        p {
            @extend %Montserrat-bold;
            font-size: 7px;
            color: black;
            letter-spacing: -.05em;
            margin: 3rem 0 0;
            text-align: center;
            &.white {
                color: white; } }
        a {
            @extend %archer-semiBold;
            cursor: default;
            background: $blue;
            border-radius: 20px;
            border: $blue;
            margin-top: 2rem;
            font-size: 16px;
            display: inline-block;
            padding-left: 1rem;
            padding-right: 1rem;
            @media screen and (min-width: 1200px) {
                font-size: 20px;
                padding-left: 2rem;
                padding-right: 2rem; } }
        h5 {
            @extend %akrobat;
            color: $blue;
            text-transform: uppercase;
            text-align: center;
            margin: 0;
            line-height: 1em;
            font-size: 40px;
            @media screen and (min-width: 1200px) {
                font-size: 50px; } } } }
.header-titular {
    width: 100%;
    position: relative;
    .box-title,
    .box-subtitle {
        background: white;
        display: inline-block;
        padding: 0 1rem;
        border-radius: 20px;
        position: relative;
        z-index: 10;
        @media screen and (min-width: 1200px) {
            padding: 0 2.5rem; }
        h2 {
            @extend %archer-semiBold;
            color: $blue;
            margin: 0;
            text-transform: uppercase; }
        &.bgceleste {
            background: $blue-light2; }
        &.bgazul {
            background: $blue;
            h2 {
                color: white; } } }
    .box-subtitle {
        background: transparent;
        border-radius: 0px;
        h2 {
            font-size: 16px;
            color: $green-light;
            text-transform: none;
            line-height: 2.5em;
            &.azul {
                color: $blue; } } }
    .linea {
        width: 50%;
        background: $green-light;
        height: 1px;
        top: 50%;
        transform: translateY(-50%);
        &.pos1 {
            right: 50%;
            width: 50%; }
        &.pos2 {
            left: 50%;
            width: 50%; } } }
